import React, {useState} from "react";
import * as styles from "./ChooseSquad.module.scss";
import * as t from "../../../assets/lang/ru.json";
import SquadItem from "../../common/SquadItem/SquadItem";
import Button from "../../base/Button/Button";
import {Link} from "react-router-dom";
import {ROUTES} from "../../App";

const ChooseSquad = () => {
  const [focusedSquad, setFocusedSquad] = useState(null);

  const squadElements = Array.from('x'.repeat(5)).map((_, i) => {
    return <SquadItem
      key={i}
      isFocused={focusedSquad === i}
      onClick={() => {
        setFocusedSquad(i);
      }}
    />
  });

  return (
    <div className={styles.root}>
      <div className={styles.title}>{t.chooseSquad}</div>
      <div className={styles.paper}>
        <div className={styles.items}>
          {squadElements}
        </div>
        <div className={styles.bottom}>
          <Link to={ROUTES.PLAY_FIELD.path}><Button>{t.acceptChoose}</Button></Link>
        </div>
      </div>
    </div>
  )
};

export default ChooseSquad;