import React from "react";
import Menu from "./views/Menu/Menu";
import ChooseSquad from "./views/ChooseSquad/ChooseSquad";
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import GameEnd from "./views/GameEnd/GameEnd";
import PlayField from "./views/PlayField/PlayField";

export const ROUTES = {
  MAIN: {
    path: "/"
  },
  CHOOSE_SQUAD: {
    path: "/choose-squad"
  },
  GAME_END: {
    path: "/game-end"
  },
  PLAY_FIELD: {
    path: "/play-field"
  },
  CARDS: {
    path: "/cards"
  }
};

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path={ROUTES.CHOOSE_SQUAD.path}><ChooseSquad/></Route>
          <Route path={ROUTES.GAME_END.path}><GameEnd/></Route>
          <Route path={ROUTES.PLAY_FIELD.path}><PlayField/></Route>
          <Route path={ROUTES.MAIN.path}><Menu/></Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
